// Feedback

export const postFeedback = (firstName, lastName, telnum, email, agree, contactType,feedback) => (dispatch) => {
    const newFeedBack = {
        firstName: firstName,
        lastName: lastName,
        telnum: telnum,
        email: email,
        agree: agree,
        contactType: contactType,
        feedback:feedback
    }
    newFeedBack.date = new Date().toISOString();
    return fetch(baseUrl + 'feedback', {
        method: 'POST',
        body: JSON.stringify(newFeedBack),
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'same-origin',
    }).then(responce => {
        if (responce.ok) {
            return responce
        }
        else {
            var error = new Error('Error ' + responce.status + ': ' + responce.statusText);
            error.responce = responce;
            throw error;
        }
    }, error => {
        var errmess = new Error(error.message);
        throw errmess
    })
        .then(responce => responce.json())
        .then(responce => dispatch(addFeedback(responce)))
        .then(responce => alert(
            "Thank you for your feedback!\n"+
            JSON.stringify(responce.payload))
        )
        .catch(error => {
            console.log('Post Feedback ', error.message);
            alert("Your feedback could not be posted\nError: " + error.message)
        });
}
export const addFeedback = (feedback) => ({
    type: ActionTypes.ADD_FEEDBACK,
    payload: feedback
})