/* eslint-disable react/jsx-pascal-case */
import React, {Component} from 'react';
import { Breadcrumb, BreadcrumbItem,Button,Label,Row, Col } from 'reactstrap'
import Link from 'react-router-dom/Link';
import { Control, Form, Errors } from 'react-redux-form'


const required = (val) => val && val.length
const maxLength = (len) => (val) => (!val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);
const isNumber = (val) => !isNaN(Number(val))
const validEmail = (val) => /^[A-Z0-9._%+-]+@[a-zA-Z0-9._%+-]+\.[A-Z]{2,4}$/i.test(val);


class Contact extends Component {
    constructor(props) {
		super(props);
		
        this.handleSubmit = this.handleSubmit.bind(this);
    }
        handleSubmit(values){
            console.log("Current state"+ JSON.stringify(values));
			// alert("Current state" + JSON.stringify(values));
			this.props.postFeedback(values.firstName, values.lastName, values.telnum, values.email, values.agree, values.contactType, values.feedback)
			this.props.resetFeedbackForm();
    }
    
    render() {
		return (
			<div className='container'>
				<div className='row'>
					<Breadcrumb>
						<BreadcrumbItem>
							<Link to='/home'>Home</Link>
						</BreadcrumbItem>
						<BreadcrumbItem active>Contact Us</BreadcrumbItem>
					</Breadcrumb>
					<div className='col-12'>
						<h3>Contact US</h3>
						<hr></hr>
					</div>
				</div>
				<div className='row row-content'>
					<div className='col-12'>
						<h3>Location Information</h3>
					</div>
					<div className='col-12 col-sm-4 offset-sm-1'>
						<h5>Our Address</h5>
						<address>
							121, Clear Water Bay Road
							<br />
							Clear Water Bay, Kowloon
							<br />
							HONG KONG
							<br />
							<i className='fa fa-phone'></i>: +852 1234 5678
							<br />
							<i className='fa fa-fax'></i>: +852 8765 4321
							<br />
							<i className='fa fa-envelope'></i>:{" "}
							<a href='mailto:confusion@food.net'>confusion@food.net</a>
						</address>
					</div>
					<div className='col-12 col-sm-6 offset-sm-1'>
						<h5>Map of our Location</h5>
					</div>
					<div className='col-12 col-sm-11 offset-sm-1'>
						<div className='btn-group' role='group'>
							<a
								role='button'
								className='btn btn-primary'
								href='tel:+85212345678'>
								<i className='fa fa-phone'></i> Call
							</a>
							<span role='button' className='btn btn-info'>
								<i className='fa fa-skype'></i> Skype
							</span>
							<a
								role='button'
								className='btn btn-success'
								href='mailto:confusion@food.net'>
								<i className='fa fa-envelope-o'></i> Email
							</a>
						</div>
					</div>
				</div>
				<div className='row row-content'>
					<div className='col-12'>
						<h3> Send Us Your Feedback</h3>
					</div>
					<div className='col-12 col-md-9'>
						<Form model='feedback' onSubmit={(values) => this.handleSubmit(values)}>
							<Row className='form-group mb-2'>
								<Label htmlFor='firstName' md={2}>
									First Name
								</Label>
								<Col md={10}>
									<Control.text
										model='.firstName'
										id='firstName'
										name='firstName'
										validators={{
											required,
											minLength: minLength(3),
											maxLength: maxLength(15),
										}}
										className='form-control'
										placeholder='First Name'
									/>
									<Errors
										className='text-danger'
										model='.firstName'
										show='touched'
										messages={{
											required: "required",
											minLength: "Must be greater than 2 characters",
											maxLength: "Must be 15 characters or less",
										}}
									/>
								</Col>
							</Row>
							<Row className='form-group mb-2'>
								<Label htmlFor='lastName' md={2}>
									Last Name
								</Label>
								<Col md={10}>
									<Control.text
										model='.lastName'
										className='form-control'
										id='lastName'
										validators={{
											required,
											minLength: minLength(3),
											maxLength: maxLength(15),
										}}
										name='lastName'
										placeholder='Last Name'
									/>
									<Errors
										className='text-danger'
										model='.lastName'
										show='touched'
										messages={{
											required: "required",
											minLength: "Must be greater than 2 characters",
											maxLength: "Must be 15 characters or less",
										}}
									/>
								</Col>
							</Row>
							<Row className='form-group mb-2'>
								<Label htmlFor='telnum' md={2}>
									Contact Tel.
								</Label>
								<Col md={10}>
									<Control.text
										model='.telnum'
										className='form-control'
										id='telnum'
										name='telnum'
										validators={{
											required,
											minLength: minLength(3),
											maxLength: maxLength(15),
											isNumber,
										}}
										placeholder='Tel. Number'
									/>
									<Errors
										className='text-danger'
										model='.telnum'
										show='touched'
										messages={{
											required: "required",
											minLength: "Must be greater than 2 numbers",
											maxLength: "Must be 15 numbers or less",
											isNumber: "must be a number",
										}}
									/>
								</Col>
							</Row>
							<Row className='form-group mb-2'>
								<Label htmlFor='email' md={2}>
									Email
								</Label>
								<Col md={10}>
									<Control.text
										model='.email'
										className='form-control'
										id='email'
										name='email'
										placeholder='Email'
										validators={{
											required,
											validEmail,
										}}></Control.text>
									<Errors
										className='text-danger'
										model='.email'
										show='touched'
										messages={{
											required: "required",
											validEmail: "Invalid email address",
										}}
									/>
								</Col>
							</Row>
							<Row className='form-group mb-2'>
								<Col md={{ size: 6, offset: 2 }}>
									<div className='form-check'>
										<Label check>
											<Control.checkbox
												model='.agree'
												className='form-check-input'
												type='checkbox'
												name='agree'
											/>{" "}
											{""}
											<strong>May we contact you?</strong>
										</Label>
									</div>
								</Col>
								<Col md={{ size: 3, offset: 1 }}>
									<Control.select
										type='select'
										model='.contactType'
										className='form-control'
										name='contactType'>
										<option>Tel.</option>
										<option>email</option>
									</Control.select>
								</Col>
							</Row>
							<Row className='form-group mb-2'>
								<Label htmlFor='message' md={2}>
									Your Feedback
								</Label>
								<Col md={10}>
									<Control.textarea
										type='textarea'
										model='.message'
										id='message'
										className='form-control'
										name='message'
										rows='12'
										placeholder=''></Control.textarea>
								</Col>
							</Row>
							<Row className='form-group mb-2'>
								<Col md={{ size: 10, offset: 2 }}>
									<Button type='submit' color='primary'>
										Send Feedback
									</Button>
								</Col>
							</Row>
						</Form>
					</div>
				</div>
			</div>
		);
	}
}

export default Contact;